/* -------------------  button soumission formulaire  ----------------------*/

button{
    border:none;
    height:49.5px;
}


.button__submit{
cursor: pointer;
text-decoration: none ;
display: flex;
align-items: center;
margin:  0;
text-transform: uppercase;
font-size: 1.5rem;
font-weight: 700;
color:$colour-fifth;
background-color: $colour-sixth;
padding: 1.1rem 3.4rem ;
border-radius: 0.7rem;
position: relative;
z-index: 1;

    &:hover{
        .button__submit-bg{
            transform: scaleX(100%);
            opacity: 1;
        }
    }
}

.button__submit-bg {
    cursor: pointer;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        border-radius: 1rem;
        background-color: darken($color: $colour-sixth, $amount: 10);
        opacity: 0;
        z-index: -1;
        transform-origin: 0% 50%;
        transform: scaleX(25%);
        transition: transform 200ms , background-color 150ms;
    }

.button__submit-bg-invalid {
    cursor: not-allowed;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    border: none;
    border-radius: 0.7rem;
    background-color: $colour-white;
    opacity: 0.5;
    z-index: 2;
}



@media screen and (max-width: 768px) {

    .btn__submit{

        margin-left: 0rem;
    }

    .button-validate{
        margin-bottom: 2.5rem;
    }
}










