.modal {

    &__wrapper{
    z-index: 100;
    background: #fff;
    border-radius: 10px;
    max-width: 700px;
    width: 80%;
    margin-top: 2rem;
    padding: 3rem 1rem;
    color: #000;
    }

  
    &__close-button {
    font-size: 4rem;
    font-weight: 700;
    color: #2f3ba9;
    cursor: pointer;
    border: none;
    background: transparent;
    position: absolute;
    top: 0rem;
    right: 0.2rem;
    }
  
  &__body-email{
    display:flex;
    justify-content: center;
  }
  &__form-wrapper{
    display:flex;
    width: 80%;
    max-width: 550px;
  }
  &__submit-button-wrapper{
    width: 80%;
    max-width: 550px;
  }
  
} 