/* -------------------  button link  ----------------------*/

.button__link{
    cursor: pointer;
    text-decoration: none ;
    display: flex;
    align-items: center;
    margin: 2rem 0;
    text-transform: uppercase;
    font-size: 1.45rem;
    font-weight: 700;
    color:$colour-primary;
    background-color: $colour-heighth;
    padding: 1.1rem 3.2rem ;
    border-radius: 1rem;
    position: relative;
    z-index: 1;
    &:hover {
        .button__link-bg {
            transform: scaleX(100%);
            opacity: 1;
        }
    }
    
    &-bg {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        border-radius: 1rem;
        background-color: darken($color: $colour-heighth, $amount: 10);
        opacity: 0;
        z-index: -1;
        transform-origin: 0% 50%;
        transform: scaleX(25%);
        transition: transform 250ms , background-color 150ms;
    }
}


