.main-stringing{
  max-width: $width-max-desktop;
  margin: auto;
  width: 80%;
}


.bg-stinging{
  position: fixed;
  top: 0; 
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  width: 100vw;
  height: calc(100vh + 7rem);
  margin-bottom: -$height-footer;
  background: url("/src/assets/balleeffet.jpeg")no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
}


.stringing-contenair{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 7rem;
  margin-bottom: 7rem;
  width:100%;
}

.breadcrump-stringing{
  max-width: 20rem;
}


/* -------------------  formulaire header ----------------------*/





.form{

  &__contenair{
  width:90%;
  background-color: white;
  color: $colour-seventh;
  font-size: 1.5rem;
  padding: 3rem 2.5rem 2rem 2.5rem;  // padding gauche droite non identique problme largeur du bandeau pour faire glissser en bas
  border-radius: 1rem;
  }

  &__header{
    &-stringing{
      display: flex;
      flex-direction: column;
      align-items: center;
    }


      &__link-home{
          display: flex;
          align-items: center;
          width: min-content;
          color: $colour-seventh;
          font-size: 1.6rem;
          text-decoration: none;
          transition: all 150ms;
          &:hover{
              text-decoration: underline;
          }
      }
          &__button-backward{
              margin-left: -5px;
              font-size: 2.5rem !important;
          }

      &__h1{
          font-size: 2.8rem;
           font-weight: bold;
           margin: 0;
           margin-top: 1rem;
      }

      &__littleBitText{
          font-weight: 500;
          font-family: "Gilroy",sans-serif;
      }
      &::after{
          cursor: default;
          content: "";
          width: 53px;
          height: 4px;
          display: block;
          margin-top: 15px;
          background-color: #2030ab;
      }
  }

}

/* -------------------  formulaire champs ----------------------*/


.form__field-wrapper{
  width: 70%;
  margin: 0 auto;
  margin-top: 3rem;
}


.form__stringing{
  cursor: default;
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 40rem;
  margin: auto;
}




.message__error{
  margin-bottom: 1rem !important;
}







.message__error{
  margin: auto;
  margin-bottom: 2rem;
}


/* -------------------  formulaire bouton ----------------------*/

.buttons-wrapper-stringing, .buttons-wrapper-ModalChange{
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.button__submit-stringing{
  margin-top: 4rem;
  margin-bottom: 2rem;
}


@media screen and (max-width: 768px) {

  .main-signup{

      width: 100%;
  }

  .signup-contenair{
      margin-bottom: 1rem;
  }
  .buttons-wrapper-signup, .buttons-wrapper-ModalChange{
      flex-direction: column;
      align-items: center;
      margin-top: 2rem;
  }

}
