

.submenu{
    &__contenair{
    animation-name:menu-hamburger-appear ;
    animation-duration: 0.5s;
    display: flex;
    justify-content: center;
    height: auto;
    width: 100%;
    position: absolute !important;
    z-index: 1;
    top: $height-header;
    left: 0rem;
    background-color:$colour-seventh ;
    padding: 2rem 0 4rem 2rem;
    text-transform: none;
    }
    &__content-width{
        display: flex;
        align-items: flex-end ;
        width: 100%;
        max-width: 122rem;
    }
    &__colum-right{

        margin-left: 60%;
    }
    &__ul {
        list-style-type: none; 
        padding-left: 0rem;     
    }

    &__li {
        list-style-type: none;
        display: flex !important;
        width: 100%;
        height: 4.5rem;
        font-size: 2rem; 
        border-bottom:0.1rem solid  $colour-primary-light ;
    }


    &__navLink__active, &__navLink__inactive {
        display: flex;
        flex-direction: row;
        align-items: center;
        font-size: 1.7rem;
        line-height: 3.2rem;
        margin-left: 0.5rem;
        color: $colour-white;
        text-transform: none;
    }

    &__navLink__inactive {
    text-decoration: none; 
    }


    &__navLink__active:hover, &__navLink__inactive:hover{
        color:yellow 
    }
}


@media screen and (max-width:768px) {

 .submenu{
    &__contenair{
        display: none;
    }
    }
}